.App {
   text-align: center;
}

.App-logo {
}

body {
   background: #FBD86E;
}

.App-header {
   height: 100px;
   padding: 20px;
   display: -ms-flexbox;
   display: flex;
   color: #693e23;
}
.header {
   -ms-flex-positive: 2;
       flex-grow: 2;
}

h1, h2 {
   color: #693e23;
}

input {
   border: none;
   margin: 0 5px 20px 0;
   height: 30px;
   font-size: 12px;
   padding-left: 5px;
}

.light-control {
   color: white;
   width: 200px;
   height: 50px;
   margin: auto;
   padding: 20px;
   margin-bottom: 10px;
   line-height: 50px;
   cursor: pointer;
   border-radius: 5px;
}

.turn-on {
   background: #27ae60;
}

.turn-off {
   background: #e74c3c;
}

.App-intro {
   font-size: large;
}

@-webkit-keyframes App-logo-spin {
   from {
	  -webkit-transform: rotate(0deg);
	          transform: rotate(0deg);
   }
   to {
	  -webkit-transform: rotate(360deg);
	          transform: rotate(360deg);
   }
}

@keyframes App-logo-spin {
   from {
	  -webkit-transform: rotate(0deg);
	          transform: rotate(0deg);
   }
   to {
	  -webkit-transform: rotate(360deg);
	          transform: rotate(360deg);
   }
}

.section {
   display: -ms-flexbox;
   display: flex;
   padding: 20px;
   margin-bottom: 5px;
   cursor: pointer;
}

.section.box {
   background: #F8B247;
}

.section.blog {
   background: #A1C4EC;
}

.section.shop {
   background: #9A817D;
}

.section.contact {
   background: #BC7036;
}

.section > div.text {
   font-size: 19px;
   color: #404040;
   padding: 14px;
}




